/* Timer style */
.timerComponent {
    padding: 50px;
    text-align: center;
    background-color: grey;
}

.eventName {
    letter-spacing: 10px;
}

.title {
    color: #fff;
}

.days,
.hours,
.minutes,
.seconds {
    color: #fff;
    font-size: '50px';
}

/* mcolist styles */
.caDetailsContainer {
    padding: 0px 10px 10px;
    color: rgba(52, 49, 76, 1) !important;
}

.colonSpan {
    float: right;
}

#simple-tabpanel-0>.MuiBox-root {
    padding: 10px 0px !important;
}

#simple-tabpanel-1>.MuiBox-root {
    padding: 0px !important;
}

.buttonRefreshContainer {
    width: 100%;
    text-align: right;
    padding-bottom: 10px;
}

.buttonRefreshContainer .refreshBtn {
    position: relative;
    right: 15px;
    top: 0;
}

.textAlignRight {
    text-align: right !important;
}

.textAlignCenter {
    text-align: center !important;
}

.elementPostionRelative {
    position: relative !important;
}

.exportMcoDataBtn {
    position: absolute;
    right: 80px;
    top: 0;
    z-index: 1;
}

.saveMcoDataBtn {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
}

.zIndex999 {
    z-index: 999 !important;
}

.floatRight {
    float: right !important;
}